@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Source+Sans+Pro:wght@300&family=Work+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0}.header_bg{object-fit:cover;width:100%;height:300px}.header_bar{position:relative;margin-top:-3.5em;padding-top:2em;padding-bottom:0.5em;background:linear-gradient(rgba(0,0,0,0), black 3em);box-shadow:0 0.3em 0.3em 0 rgba(0,0,0,0.75);text-align:center}.header_bar h1{font-family:'Open Sans', sans-serif;font-size:3em;color:#b87333}.header_bar h2{font-family:'Source Sans Pro', sans-serif;font-size:1em;color:#A9A49D}.body{margin:2em auto;margin-bottom:4em;width:80%}.body_synopsis{margin:auto;width:90%;max-width:22em;text-align:center;font-size:1.2em;padding-bottom:2em}.section{margin:0 auto 2em auto;max-width:34em}.section p{margin:1em}.section img{width:100%;object-fit:contain}.section li{margin:0.5em}.videoContainer{margin:1em;position:relative;padding-bottom:56.25%}.videoContainer iframe{position:absolute}.gist_content .gist-file{max-height:500px;overflow-y:scroll}

